html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Ruluko", sans-serif;
  font-size: 1.6rem;
  background-color: #EBE7DE;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: #000;
}

[class*=__contenedor] {
  max-width: 120rem;
  margin: 0 auto;
  width: 90%;
}

h1 {
  color: #000;
  line-height: 1;
  margin: 0;
  font-size: 4rem;
  font-family: "Montserrat", sans-serif;
}

h2 {
  color: #C39C63;
  line-height: 1.5;
  font-size: 3.6rem;
  font-family: "Lora", serif;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
}
h2 span {
  display: block;
  font-size: 1.8rem;
  font-weight: 400;
}

h3 {
  color: #000;
  line-height: 2;
  font-size: 3rem;
  font-family: "Lora", serif;
  font-weight: 700;
  margin: 0;
}

p {
  color: #000;
  font-size: 1.8rem;
  line-height: 2;
  font-family: "Ruluko", sans-serif;
  margin: 0;
}

a {
  font-family: "Lora", serif;
  font-weight: 400;
}

.header {
  background-image: url(../img/hero.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left;
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .header {
    background-position: top;
    padding-top: 4rem;
  }
}
@media (min-width: 768px) {
  .header__barra {
    display: flex;
    justify-content: space-between;
  }
}
.header__logo {
  margin-inline: auto;
  width: 25rem;
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
    width: 15rem;
  }
}
@media (min-width: 1024px) {
  .header__logo {
    width: 20rem;
  }
}
@media (min-width: 1440px) {
  .header__logo {
    width: 25rem;
  }
}
.header__titulo-logo {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
}
@media (min-width: 768px) {
  .header__titulo-logo {
    font-size: 2.5rem;
  }
}
@media (min-width: 1024px) {
  .header__titulo-logo {
    font-size: 3rem;
  }
}
@media (min-width: 1440px) {
  .header__titulo-logo {
    font-size: 4rem;
  }
}
.header__navegacion {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .header__navegacion {
    margin: 0;
    display: flex;
    align-items: center;
    gap: 1.5rem;
  }
}
@media (min-width: 1024px) {
  .header__navegacion {
    gap: 2rem;
  }
}
.header__enlace {
  display: block;
  text-align: center;
  text-transform: uppercase;
  font-size: 2.5rem;
  font-family: "Lora", serif;
  font-weight: 700;
  color: #ECEEEF;
  margin-bottom: 4rem;
  transition-property: transform;
  transition-duration: 300ms;
}
.header__enlace:last-of-type {
  margin-bottom: 0;
}
.header__enlace:hover {
  cursor: pointer;
  color: #C39C63;
  transform: scale(1.2);
}
@media (min-width: 768px) {
  .header__enlace {
    margin-bottom: 0;
    font-size: 1.8rem;
  }
}
@media (min-width: 1024px) {
  .header__enlace {
    font-size: 2rem;
  }
}
.header__eslogan {
  padding-block: 10rem;
  max-width: 40rem;
}
@media (min-width: 768px) {
  .header__eslogan {
    padding-block: 15rem;
    max-width: 60rem;
  }
}
.header__eslogan-texto {
  margin: 0;
  line-height: 1.5;
  color: #ECEEEF;
  font-size: 4rem;
  font-family: "Lora", serif;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 768px) {
  .header__eslogan-texto {
    text-align: left;
  }
}
@media (min-width: 1440px) {
  .header__eslogan-texto {
    font-size: 6rem;
  }
}

.testimonial {
  margin-block: 5rem;
}
.testimonial .testimonial__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .testimonial .testimonial__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    align-items: center;
  }
}
.testimonial .testimonial__descripcion {
  text-align: center;
  max-width: 30rem;
  margin-inline: auto;
}
.testimonial .testimonial__card {
  background-color: #fff;
  width: 30rem;
  margin-inline: auto;
  padding: 2rem 4rem;
  text-align: center;
  box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.6);
}
.testimonial .testimonial__autor {
  margin: 0 0 2rem 0;
  text-transform: uppercase;
  font-size: 2rem;
  line-height: 1.5;
}
.testimonial .testimonial__opinion {
  margin: 0;
}

.equipos {
  background-color: #5E311E;
  padding-block: 5rem;
}
.equipos__titulo span {
  color: #EBE7DE;
}
.equipos__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .equipos__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .equipos__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5rem;
  }
}
.equipos__texto {
  color: #ECEEEF;
  text-align: center;
  margin: 2rem 0 0 0;
}
@media (min-width: 768px) {
  .equipos__texto {
    text-align: left;
  }
}
@media (min-width: 1024px) {
  .equipos__texto {
    grid-column: 1/3;
  }
}
.equipos__imagen {
  box-shadow: -2px 2px 10px 2px rgba(0, 0, 0, 0.5);
}

.discos {
  padding-block: 5rem;
}
.discos__titulo span {
  color: #5E311E;
}
.discos__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .discos__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .discos__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5rem;
  }
}
.discos__texto {
  text-align: center;
  margin: 2rem 0 0 0;
}
@media (min-width: 768px) {
  .discos__texto {
    text-align: left;
    grid-column: 2/3;
  }
}
@media (min-width: 1024px) {
  .discos__texto {
    grid-column: 2/4;
  }
}
.discos__imagen {
  box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.5);
}
@media (min-width: 768px) {
  .discos__imagen {
    grid-row: 1/2;
    grid-column: 1/2;
  }
}

.planes {
  background-color: #5E311E;
  padding-block: 5rem;
}
.planes__titulo span {
  color: #EBE7DE;
}
.planes__descripcion {
  text-align: center;
  color: #ECEEEF;
  margin: 2rem 0;
}
.planes__cards {
  display: flex;
  flex-direction: column;
  gap: 5rem;
}
@media (min-width: 768px) {
  .planes__cards {
    flex-direction: row;
    gap: 0;
    justify-content: center;
    align-items: center;
    margin-top: 5rem;
  }
}
.planes__card {
  background-color: #EBE7DE;
  text-align: center;
  max-width: 25rem;
  margin-inline: auto;
  padding: 1rem 2rem;
  border-radius: 5rem;
  transition-property: transform;
  transition-duration: 300ms;
}
.planes__card:hover {
  transform: scale(1.1);
}
@media (min-width: 768px) {
  .planes__card {
    width: auto;
    margin: 0;
  }
}
.planes__card--master {
  order: -1;
  background-color: #cec4ae;
}
@media (min-width: 768px) {
  .planes__card--master {
    order: 0;
    z-index: 1;
    transform: scale(1.1);
  }
  .planes__card--master:hover {
    transform: scale(1.18);
  }
}
.planes__nombre {
  margin: 0;
  text-transform: uppercase;
  color: #5E311E;
}
.planes__nombre--master {
  color: #4b2718;
}
.planes__precio {
  margin: 0;
  font-size: 4rem;
  color: #C39C63;
  line-height: 1.5;
}
.planes__precio--master {
  color: #8b6835;
}
.planes__listado {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.planes__enlace {
  display: inline-block;
  margin-block: 2rem;
  background-color: #C39C63;
  font-size: 2rem;
  padding: 2rem;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 0.3rem;
  transition-property: background-color;
  transition-duration: 300ms;
}
.planes__enlace:hover {
  background-color: #b08444;
  color: #e6e6e6;
}
.planes__enlace--master {
  background-color: #9d763c;
  color: #d9d9d9;
}
.planes__enlace--master:hover {
  background-color: #785a2e;
  color: #bfbfbf;
}

.box {
  padding-block: 5rem;
}
.box__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 5rem;
  align-items: center;
}
@media (min-width: 768px) {
  .box__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.box__titulo span {
  color: #5E311E;
}
.box__texto {
  text-align: center;
  margin-bottom: 2rem;
}
.box__listado {
  text-align: center;
}
.box__item {
  margin-bottom: 1.5rem;
}
.box__item:last-of-type {
  margin-bottom: 0;
}
.box__imagen {
  grid-row: 1/2;
  margin: 2rem auto 0 auto;
  width: 30rem;
}
@media (min-width: 768px) {
  .box__imagen {
    grid-column: 2/3;
  }
}

.productos {
  background-color: #5E311E;
  padding-block: 5rem;
}
.productos__titulo span {
  color: #EBE7DE;
}
.productos__grid {
  margin-top: 2rem;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .productos__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .productos__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.producto {
  background-color: #ECEEEF;
  max-width: 45rem;
}
.producto__imagen {
  aspect-ratio: 3/2;
  background-size: 100% 150%;
  position: relative;
}
.producto:nth-child(1) .producto__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/producto-1.jpg");
}
.producto:nth-child(2) .producto__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/producto-2.jpg");
}
.producto:nth-child(3) .producto__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/producto-3.jpg");
}
.producto:nth-child(4) .producto__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/producto-4.jpg");
}
.producto:nth-child(5) .producto__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/producto-5.jpg");
}
.producto:nth-child(6) .producto__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%), url("../img/producto-6.jpg");
}
.producto__titulo {
  position: absolute;
  left: 1rem;
  bottom: 1rem;
  margin: 0;
  color: #C39C63;
  line-height: 1;
  text-transform: uppercase;
}
.producto__contenido {
  padding: 2rem;
  text-align: center;
}
.producto__descripcion {
  margin: 0;
  line-height: 1.5;
}
.producto__precio {
  margin: 0;
  padding-block: 1.5rem;
  font-size: 2.8rem;
  color: #5E311E;
}
.producto__enlace {
  display: inline-block;
  margin-block: 2rem;
  background-color: #C39C63;
  font-size: 2rem;
  padding: 2rem;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 0.3rem;
  transition-property: background-color;
  transition-duration: 300ms;
  margin: 0;
}
.producto__enlace:hover {
  background-color: #b08444;
  color: #e6e6e6;
}

.suscripcion {
  padding-block: 5rem;
}
.suscripcion__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 5rem;
  align-items: center;
}
@media (min-width: 768px) {
  .suscripcion__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.suscripcion__contenido {
  text-align: center;
}
.suscripcion__texto {
  font-size: 2.8rem;
  line-height: 1.5;
  margin: 0;
}
.suscripcion__enlace {
  display: inline-block;
  margin-block: 2rem;
  background-color: #C39C63;
  font-size: 2rem;
  padding: 2rem;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 0.3rem;
  transition-property: background-color;
  transition-duration: 300ms;
}
.suscripcion__enlace:hover {
  background-color: #b08444;
  color: #e6e6e6;
}

.footer {
  background-color: #5E311E;
  padding-top: 5rem;
}
.footer__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5rem;
  }
}
.footer__widget {
  text-align: center;
}
.footer__titulo {
  margin: 0;
  color: #C39C63;
}
.footer__navegacion {
  margin-top: 2rem;
}
.footer__enlace {
  display: block;
  margin-bottom: 2rem;
  color: #EBE7DE;
  transition-property: transform;
  transition-duration: 300ms;
}
.footer__enlace:last-of-type {
  margin-bottom: 0;
}
.footer__enlace:hover {
  color: #C39C63;
  transform: scale(1.1);
}
.footer__texto {
  max-width: 40rem;
  margin-inline: auto;
  color: #EBE7DE;
}
.footer__logo {
  width: 20rem;
  margin-inline: auto;
}
.footer__titulo-logo {
  text-transform: uppercase;
  font-size: 3rem;
  color: #fff;
}
.footer__copyright {
  text-align: center;
  color: #EBE7DE;
  padding-block: 2rem;
}